import React, { CSSProperties, FC } from 'react';

interface Props {
	children: string;
	x: number;
	y: number;
	rotate?: boolean;
}

const Street: FC<Props> = (props: Props) => {
	const style: CSSProperties = {
		fontFamily: 'interstate, sans-serif',
		fontWeight: 800,
		fontSize: 12,
		textTransform: 'uppercase'
	};

	return (
		<text
			style={style}
			x={props.x}
			y={props.y}
			transform={props.rotate === true ? `rotate(90 ${props.x} ${props.y})` : 'rotate(0)'}
		>
			{props.children}
		</text>
	);
};

export default Street;
